<template>
  <div class="parallax h-100 w-100">
    <!-- floating div -->
    <div
      class="floating-div"
      :style="floatingDivStyle">
      <MainContent></MainContent>
    </div>
  </div>
</template>

<script>
import MainContent from '@/components/MainContent.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'Home',

  components: {
    MainContent
  },

  created() {
  },

  computed: {
    ...mapGetters([
      'isSmallWidthMode',
    ]),

    floatingDivStyle() {
      return {
        width: `calc(100% - ${this.isSmallWidthMode ? 56 : 256}px)`
      };
    }
  },

}
</script>

<style scoped>
.left-pane {
  min-width: 250px;
  width: 30%;
  max-width: 30%;
}

.floating-div {
  position: fixed;
  height: 100vh;
  overflow-y: hidden;
}
</style>