<template>
  <div
    class="h-100 w-100 d-flex align-center justify-center"
    @click="clickMainContent()">
    <v-card
      class="floating-card"
      color="primary">
      <div class="h-100 d-flex flex-row flex-wrap align-content-between">
        <v-card-title class="w-100 mb-auto display-2">
          <h2> {{ pageContent.title }} </h2>
        </v-card-title>
        <v-card-text class="w-100 headline">
          <div>
            <!-- check for content type -->
            <div
              v-for="(content, index) in pageContent.content"
              :key="index">
              •
              <a
                v-if="typeof content !== 'string'"
                class="item-link"
                :href="content.link"
                target="_blank">
                {{ getContentText(content) }}
              </a>
              <span v-else>
                {{ getContentText(content) }}
              </span>
            </div>
          </div>
        </v-card-text>
        <v-card-actions class="w-100 mt-auto">
          <Footer></Footer>
        </v-card-actions>
      </div>
      
    </v-card>
  </div>
</template>

<script>
import { actionTypes } from '@/store/landing/actions'
import { mapGetters } from 'vuex'
import Footer from '@/components/Footer.vue'

export default {
  name: 'MainContent',

  components: {
    Footer
  },

  computed: {
    ...mapGetters([
      'contactMeDetails',
      'sideNavOpen',
    ]),

    pageContent() {
      return this.$store.getters.pageContent(this.$route.hash ? this.$route.hash : '#landing');
    }
  },

  methods: {
    getContentText(content) {
      return `${this.extractContentText(content)}`;
    },

    extractContentText(content) {
      return typeof content === 'string' ? content : content.text;
    },
    
    clickMainContent() {
      if (this.sideNavOpen) {
        this.$store.dispatch(actionTypes.toggleSideNav);
      }
    }
  }
}
</script>

<style scoped>
.floating-card {
  min-width: 300px;
  max-width: 90% !important; 
  width: 90%;
  min-height: 300px;
  max-height: 80%;
  height: 80%;
  border-radius: 0 !important;
  position: absolute;
  overflow-y: auto;
}

.v-card__text, .v-card__title {
  word-break: normal !important;
}

.v-card_text {
  height: 50%;
  max-height: 50%;
}

.item-link {
  color: #000000;
}
</style>